  --zone-border-hand: #d1d5db;
  --zone-bg-grave : #4f4949;
  --zone-border-grave: #a0a0a0;
  --zone-bg-extra : #534156;
  --zone-border-extra: #c5a9cc;
  --zone-bg-monster: #726249;
